<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :getDataCb="getDataCb"
      :singleExpand="true"
      :showExpand="true"
    >
      <template v-slot:expansionIcon="{ expand, isExpanded, item }">
        <tooltip-icon-button
          btnTooltip="Assign Schedule Action"
          btnIcon="mdi-calendar"
          btnColor="orange"
          buttonSize="medium"
          :onClick="() => onExpandClick({ expand, isExpanded, item })"
        ></tooltip-icon-button>
      </template>
      <template v-slot:expansionView="{ headers, item }">
        <div>
          <v-progress-circular
            size="20"
            indeterminate
            v-if="!selectedCalendarData && isLoadingCalendar"
          ></v-progress-circular>
          <calendar-view
            v-if="selectedCalendarData"
            :key="renderCalendarView"
            :data="selectedCalendarData"
            :config="selectedCalendarConfig"
            :isLoadingCalendar="isLoadingCalendar"
            :updateData="(data, config) => updateCalendarView(item.uuid, data, config)"
          ></calendar-view>
        </div>
      </template>

      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <rf-schedules-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-schedules-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <rf-schedules-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-schedules-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import RfSchedulesForm from './rfSchedulesForm.vue';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import CalendarView from '../RfEvents/CalendarView.vue';
import * as gridNames from '../../../constants/gridNames';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfScheduleService } from '../../../services/rfScheduleService';

const dialogNames = {
  viewPoints: 'viewPoints',
};

const redirectMap = {
  [dialogNames.viewPoints]: (networkId, deviceId) =>
    ROUTES.rfPoints.replace(':networkId', networkId).replace(':deviceId', deviceId),
};

export default {
  name: 'rf-schedules-grid',
  components: { GenericGrid, RfSchedulesForm, IconButton, TextButton, TooltipIconButton, CalendarView },
  props: ['params', 'getDataCb'],
  data() {
    return {
      selectedCalendarData: null,
      renderCalendarView: 0,
      selectedCalendarConfig: {},
      isLoadingCalendar: false,
      gridName: gridNames.RF_DEVICES_GRID,
      dialogNames: dialogNames,
      service: rfScheduleService,
      toolbarTitle: 'Schedules',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    onExpandClick({ expand, isExpanded, item }) {
      const value = !isExpanded;
      expand(value);
      if (value) {
        this.selectedItem = item;
        this.selectedCalendarConfig = { ...this.selectedItem.schedule.config };
        this.selectedCalendarData = { ...this.selectedItem.schedule.schedules };
        this.fetchCalendarData();
      } else {
        this.selectedCalendarData = null;
      }
    },
    updateCalendarView(uuid, calendarModel, calendarConfig) {
      const payload = {
        schedules: calendarModel,
        config: calendarConfig,
      };
      return rfScheduleService.patchSchedule(uuid, payload).then(data => {
        this.fetchCalendarData();
        return data;
      });
    },
    fetchCalendarData() {
      const selectedUUID = this.selectedItem.uuid;
      this.isLoadingCalendar = true;
      return rfScheduleService
        .fetchById(selectedUUID)
        .then(data => {
          const schedules = (data && data.schedule && data.schedule.schedules) || {};
          const config = (data && data.schedule && data.schedule.config) || {};
          this.selectedCalendarConfig = { ...config };
          this.selectedCalendarData = { ...schedules };
          this.renderCalendarView++;
          return data;
        })
        .finally(() => {
          this.isLoadingCalendar = false;
        });
    },
  },
};
</script>
