<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <span>Rf Schedules</span>
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <rf-schedules-grid :getDataCb="getDataCb"> </rf-schedules-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import IconButton from '../../Common/IconButton.vue';
import RfSchedulesForm from './rfSchedulesForm.vue';
import rfSchedulesGrid from './rfSchedulesGrid.vue';
import routeConstant from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

const baseHref = routeConstant.rfSchedules;
const initialBreadCrumbItem = {
  text: 'Schedules',
  disabled: false,
  link: baseHref,
};

export default {
  name: 'rf-schedules',
  components: {
    IconButton,
    RfSchedulesForm,
    rfSchedulesGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
      },
      immediateParent: null,
      renderCount: 0,
      networkName: '',
      gridData: {},
      breadcrumbItems: [initialBreadCrumbItem],
    };
  },
  mounted() {},
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push(initialBreadCrumbItem);
    },
  },
};
</script>
