<template>
  <v-card>
    <v-card outlined class="pa-3">
      <v-card-title class="mb-5">
        <v-btn @click="onCancel" icon medium class="mr-1">
          <v-icon medium> mdi-arrow-left </v-icon>
        </v-btn>
        <span class="headline">{{ title }}</span>
      </v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-row dense>
            <v-col cols="12">
              <v-text-field required v-model="formValues.name" label="Name" :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-switch v-model="formValues.enable" label="Enable"></v-switch>
            </v-col>
            <v-col cols="4">
              <v-switch v-model="formValues.is_active" label="Active"></v-switch>
            </v-col>
            <v-col cols="4">
              <v-switch v-model="formValues.is_global" label="Global"></v-switch>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn v-if="onCancel" color="darken-1" text @click="onCancel"> Cancel </v-btn>
        <v-btn color="blue darken-1" text @click="onSubmit(formValues)" :loading="isSaving"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-card>
</template>
<script>

const getFormValues = (item = {}) => ({
  name: item.name || '',
  enable: item.enable || false,
  is_active: item.is_active || false,
  is_global: item.is_global || false,
  schedule: item.schedule || {},
});
import _cloneDeep from 'lodash/cloneDeep';
export default {
  name: 'rf-device-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item'],
  data: () => ({
    valid: false,
    formValues: {},
    rerender: 0,
    isSaving: false,
    parentModulePath: null,
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
  }),
  computed: {
    lowerCaseTitle() {
      return 'schedules';
    },
    titleCase() {
      return 'Schedules';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (!this.isEdit) {
      this.$refs.form.reset();
    }
    this.formValues = getFormValues(_cloneDeep(this.item));
    this.selectedCalendarConfig = { ...this.formValues.schedule.config };
    this.selectedCalendarData = { ...this.formValues.schedule.schedules };
    this.renderCalendarView++;
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onSubmit() {
      this.isSaving = true;
      if (typeof this.onSave !== 'function') {
        return Promise.reject(false);
      }
      return this.onSave(this.formValues)
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.title}.`);
          this.cleanUp();
        })
        .catch(() => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.title}.`, {
            type: 'error',
          });
        })
        .finally(() => {
          this.isSaving = false;
        });
    },
  },
};
</script>
